/* Example

@font-face {
  font-family: 'CustomFont';
  src: url('../fonts/Custom-Font.eot');
  src: url('../fonts/Custom-Font.eot?#iefix') format('embedded-opentype'),
     url('../fonts/Custom-Font.woff') format('woff'),
     url('../fonts/Custom-Font.svg#custom_font') format('svg');
  font-weight: 400;       // For normal width. It could has another value   
  font-style: normal;     // Also could has another value
  }


// Var for using custom font
$CustomFont: 'CustomFont', Helvetica, Arial, sans-serif;
*/

@font-face {
  font-family: "YS Text";
  src: url("%=static=%../fonts/YS Text-Light.eot");
  src: url("%=static=%../fonts/YS Text-Light.eot?#iefix")
      format("embedded-opentype"),
    url("%=static=%../fonts/YS Text-Light.woff2") format("woff2"),
    url("%=static=%../fonts/YS Text-Light.woff") format("woff"),
    url("%=static=%../fonts/YS Text-Light.ttf") format("truetype"),
    url("%=static=%../fonts/YS Text-Light.svg#YSText-Light") format("svg");
  font-weight: 300;
  font-style: normal;
}

